<template>
  <div>
    <p>Find Value By Key From Json</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <input v-model="key">
    <textarea v-model="source" id="source" rows="50" cols="120" placeholder="source text"></textarea>
    <textarea v-model="output" id="output" rows="50" cols="120" placeholder="output" readonly></textarea>
    count: {{result_count}}
    <br>
    <div>
      <input value="handle" type="button" @click="handle()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'FindValueByKeyFromJsonView',
  data () {
    return {
      key: '',
      source: '',
      output: '',
      result_count: 0
    }
  },
  methods: {
    isObj (obj) {
      return obj instanceof Object
    },
    handle () {
      this.result_count = 0
      const json = JSON.parse(this.source)
      let result = ''
      result = this.handleJson(json, result)
      this.output = result
    },
    handleJson (obj, result) {
      for (const k in obj) {
        if (this.isObj(obj[k])) {
          result = this.handleJson(obj[k], result)
        } else if (k === this.key) {
          result += obj[k] + '\n'
          this.result_count += 1
        }
      }
      return result
    }
  }
}

</script>
